/* Never Miss a Call Section (screenshot accurate) */
.never-miss-section {
  background: #f7f1fd;
  border-radius: 2rem;
  margin: 48px 0 0 0;
  padding: 56px 32px 56px 32px;
  color: #2d0b3a;
  box-sizing: border-box;
}
.never-miss-title {
  text-align: center;
  font-size: 2.2rem;
  font-weight: 800;
  margin-bottom: 48px;
  color: #2d0b3a;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  line-height: 1.15;
}
.never-miss-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  max-width: 1100px;
  margin: 0 auto;
}
.never-miss-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: none;
  border-radius: 1.5rem;
  padding: 0 16px 0 16px;
}
.never-miss-icon {
  width: 56px;
  height: 56px;
  background: #e6d8fa;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 18px;
}
.never-miss-check {
  color: #a47be7;
  font-size: 2.2rem;
  font-weight: 700;
}
.never-miss-desc {
  font-size: 1.08rem;
  font-weight: 600;
  color: #2d0b3a;
  text-align: center;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
}

@media (max-width: 900px) {
  .never-miss-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  .never-miss-section {
    padding: 32px 8px 32px 8px;
  }
}
