/* Features Grid Section (screenshot accurate) */
.features-grid-section {
  background: linear-gradient(120deg, #2d0b3a 60%, #3a1760 100%);
  border-radius: 2rem;
  margin: 48px 0 0 0;
  padding: 56px 32px 56px 32px;
  color: #fff;
  box-sizing: border-box;
}
.features-grid-title {
  text-align: center;
  font-size: 2.2rem;
  font-weight: 800;
  margin-bottom: 48px;
  color: #fff;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  line-height: 1.15;
}
.features-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 32px;
  max-width: 1100px;
  margin: 0 auto;
}
.feature-card {
  background: rgba(44, 18, 70, 0.95);
  border-radius: 1.5rem;
  padding: 36px 32px 32px 32px;
  display: flex;
  align-items: flex-start;
  gap: 24px;
  box-shadow: 0 2px 16px rgba(108,71,255,0.07);
  min-height: 170px;
}
.feature-icon {
  flex-shrink: 0;
  margin-top: 2px;
}
.feature-card-title {
  font-size: 1.18rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 8px;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
}
.feature-card-desc {
  font-size: 1.05rem;
  color: #e2d6f7;
  font-weight: 400;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
}

@media (max-width: 900px) {
  .features-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  .features-grid-section {
    padding: 32px 8px 32px 8px;
  }
}

.home {
  padding: 0;
  background: #f4f4fa;
}


/* Hero Section (screenshot accurate) */
.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 56px 48px 56px 48px;
  background: linear-gradient(120deg, #f7f6ff 60%, #ede6fa 100%);
  border-radius: 2.5rem;
  margin: 32px 0 0 0;
  min-height: 480px;
  box-sizing: border-box;
  gap: 32px;
}
.hero-content {
  flex: 1 1 480px;
  max-width: 540px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.hero-badge-row {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
}
.hero-badge {
  background: #ede6fa;
  color: #9332e0;
  font-size: 0.95rem;
  font-weight: 700;
  border-radius: 1.2rem;
  padding: 4px 16px;
  margin-right: 4px;
  letter-spacing: 0.04em;
}
.hero-badge-text {
  color: #9332e0;
  font-size: 1rem;
  font-weight: 500;
}
.hero-main-title {
  font-size: 2.8rem;
  font-weight: 800;
  color: #23272F;
  margin-bottom: 18px;
  line-height: 1.1;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
}
.hero-main-desc {
  font-size: 1.18rem;
  color: #6b6b6b;
  margin-bottom: 32px;
  font-weight: 500;
}
.hero-main-btn {
  display: inline-block;
  background: #9332e0;
  color: #fff;
  font-weight: 700;
  font-size: 1.18rem;
  border-radius: 2rem;
  padding: 1.1rem 2.5rem;
  box-shadow: 0 2px 16px rgba(108,71,255,0.10);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  border: none;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  font-family: inherit;
  margin-top: 8px;
}
.hero-main-btn:hover {
  background: #6C47FF;
  color: #fff;
  box-shadow: 0 4px 24px rgba(108,71,255,0.18);
}
.hero-image {
  flex: 1 1 420px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  min-width: 320px;
}
.hero-image-mockup {
  width: 370px;
  height: 320px;
  border-radius: 2rem;
  background: linear-gradient(120deg, #6C47FF 0%, #9332e0 100%);
  box-shadow: 0 4px 32px rgba(108,71,255,0.13);
}

@media (max-width: 1100px) {
  .hero-section {
    flex-direction: column;
    padding: 32px 12px 32px 12px;
    gap: 32px;
  }
  .hero-image {
    justify-content: center;
  }
}

/* Features Section */
.features-section {
  background: #fff;
  padding: 3rem 1rem 2rem 1rem;
  border-radius: 2rem;
  max-width: 1100px;
  margin: -4rem auto 2rem auto;
  box-shadow: 0 2px 16px rgba(108,71,255,0.07);
  text-align: center;
}
.features-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
}
.feature-item {
  background: #f7f7ff;
  border-radius: 1rem;
  padding: 2rem 1.5rem;
  flex: 1 1 250px;
  min-width: 250px;
  max-width: 320px;
  box-shadow: 0 1px 4px rgba(108,71,255,0.04);
}
.feature-item h3 {
  color: #6C47FF;
  margin-bottom: 0.5rem;
}

/* Testimonials Section */
.testimonials-section {
  background: #fff;
  padding: 3rem 1rem 2rem 1rem;
  border-radius: 2rem;
  max-width: 900px;
  margin: 2rem auto;
  text-align: center;
  box-shadow: 0 2px 16px rgba(108,71,255,0.07);
}
.testimonials-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
}
.testimonial-item {
  background: #f7f7ff;
  border-radius: 1rem;
  padding: 1.5rem 1rem;
  min-width: 220px;
  max-width: 320px;
  font-style: italic;
  color: #333;
}
.testimonial-item span {
  display: block;
  margin-top: 1rem;
  color: #6C47FF;
  font-weight: 500;
}

/* Pricing Section */
.pricing-section {
  background: #fff;
  padding: 3rem 1rem 2rem 1rem;
  border-radius: 2rem;
  max-width: 900px;
  margin: 2rem auto;
  text-align: center;
  box-shadow: 0 2px 16px rgba(108,71,255,0.07);
}
.pricing-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
}
.pricing-item {
  background: #f7f7ff;
  border-radius: 1rem;
  padding: 2rem 1.5rem;
  min-width: 220px;
  max-width: 320px;
  box-shadow: 0 1px 4px rgba(108,71,255,0.04);
  position: relative;
}
.pricing-item.popular {
  border: 2px solid #6C47FF;
  box-shadow: 0 4px 16px rgba(108,71,255,0.12);
}
.pricing-item h3 {
  color: #6C47FF;
  margin-bottom: 0.5rem;
}
.price {
  font-size: 2rem;
  font-weight: bold;
  color: #6C47FF;
  margin: 1rem 0;
}
.pricing-btn {
  display: inline-block;
  margin-top: 1.5rem;
  padding: 0.7rem 2rem;
  background: #6C47FF;
  color: #fff;
  border-radius: 2rem;
  font-weight: bold;
  text-decoration: none;
  font-size: 1rem;
  transition: background 0.2s, color 0.2s;
}
.pricing-btn:hover {
  background: #B47AFF;
  color: #fff;
}

/* FAQ Section */
.faq-section {
  background: #fff;
  padding: 3rem 1rem 2rem 1rem;
  border-radius: 2rem;
  max-width: 900px;
  margin: 2rem auto;
  text-align: center;
  box-shadow: 0 2px 16px rgba(108,71,255,0.07);
}
.faq-list {
  margin-top: 2rem;
  text-align: left;
}
.faq-item {
  margin-bottom: 1.5rem;
}
.faq-item h4 {
  color: #6C47FF;
  margin-bottom: 0.5rem;
}

/* Footer */
.footer {
  background: #6C47FF;
  color: #fff;
  padding: 2rem 1rem;
  text-align: center;
  border-radius: 2rem 2rem 0 0;
  margin-top: 3rem;
}
.footer-content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  align-items: center;
  font-size: 1rem;
}